@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP);
body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-image: url(/star-wars/static/media/star-wars.ec0b4348.jpg);
  background-size: cover;
}


@font-face {
    font-family:"STARWARS";
    src:url(/star-wars/static/media/STARWARS.35f31c7a.woff) format("woff");
    font-weight: normal;
    font-style: normal;
}


h1, h3, body {
    font-family: 'STARWARS';
    text-transform: uppercase;
}

h1, h2, h3 {
color: goldenrod;
}

nav {
    color: goldenrod;
}

.particles {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}


